
.gear img[data-v-0e179bfd]{
  width: 250px;
  height: 250px;
}
.RotateAW[data-v-0e179bfd]{
  -webkit-animation: RotateAW  5s linear infinite;
          animation: RotateAW  5s linear infinite;
}
.paused[data-v-0e179bfd]{
  -webkit-animation-play-state:paused;
          animation-play-state:paused;
}
@-webkit-keyframes RotateAW{
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes RotateAW{
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}




