
.round[data-v-4099e890]{
  position: relative;
  width: 250px;
  height: 250px;
}
/*圆的大小1920为300px，1280为250px；响应式*/
.round img[data-v-4099e890]{
  width:250px;
  height: 250px;
}
.round__outside[data-v-4099e890]{
  position: absolute;
  top: 0;
}
.round__inside[data-v-4099e890]{
  position: absolute;
  top: 0;
}
.round__cent[data-v-4099e890]{
  position: absolute;
  top: 0;
}

/*顺逆时针转动动画*/
.RotateAW[data-v-4099e890]{
  -webkit-animation: RotateAW  3s linear infinite;
          animation: RotateAW  3s linear infinite;
}
.RotateCW[data-v-4099e890]{
  -webkit-animation: RotateCW  3s linear infinite;
          animation: RotateCW  3s linear infinite;
}
.paused[data-v-4099e890]{
  -webkit-animation-play-state:paused;
          animation-play-state:paused;
}
.round__title[data-v-4099e890]{
  width:125px;
  height: 125px;
  cursor:pointer;
  position:absolute;
  top:63px;
  left:63px;
  border-radius:50%;
}
.round__title[data-v-4099e890]:hover{
  background-color:rgba(10, 218, 217,0.3);
}
.round__title p[data-v-4099e890]{
  color:white;
  text-align: center;
  line-height:75px;
}
@-webkit-keyframes RotateAW{
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes RotateAW{
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@-webkit-keyframes RotateCW{
from {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
to {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
}
@keyframes RotateCW{
from {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
to {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
}



